import React, { useState, useEffect } from "react";
import { TransferToSelf, TransferToPerson, TransferToBusiness } from "./index";

const navigation = [
  {
    name: "U2S",
    link: "#u2s"
  },
  {
    name: "U2U",
    link: "#u2u"
  },
  {
    name: "U2B",
    link: "#u2b"
  }
];

export default args => {
  const data = args.data;
  const format = args.format;
  const type = args.type;
  const onTypeChange = args.onTypeChange;

  const [transferCard, setTransferCard] = useState(null);

  const onNavigationClick = id => {
    onTypeChange(id);
  };

  useEffect(() => {
    if (type === null) return;

    // setTransferCard()
    switch (type) {
      case "#u2s": // User to self
        setTransferCard(
          <TransferToSelf
            data={data}
            format={format}
            navigation={navigation}
            navigationdefaultActiveKey={type}
            onNavigationClick={onNavigationClick}
          />
        );
        break;
      case "#u2u": // User to user
        setTransferCard(
          <TransferToPerson
            data={data}
            format={format}
            navigation={navigation}
            navigationdefaultActiveKey={type}
            onNavigationClick={onNavigationClick}
          />
        );
        break;
      case "#u2b": // User to business
        setTransferCard(
          <TransferToBusiness
            data={data}
            format={format}
            navigation={navigation}
            navigationdefaultActiveKey={type}
            onNavigationClick={onNavigationClick}
          />
        );
        break;
      default:
        break;
    }

    // eslint-disable-next-line
  }, [data, format, navigation, type]);

  return <>{transferCard}</>;
};
