import React from "react";
import { useTranslation } from "react-i18next";
import { Form, Button } from "react-bootstrap";
import Card from "../card";

export default args => {
  // let data = args.data;
  const userData = args.data.userData;
  let format = args.format;
  const { t } = useTranslation("Transfer");
  // let footer = <small className="text-muted">Last updated 3 mins ago</small>;

  const available =
    userData !== null
      ? format(userData.acc_curr + userData.acc_cred + userData.acc_save)
      : null;
  let acc_curr = userData !== null ? format(userData.acc_curr) : null;
  let acc_cred = userData !== null ? format(userData.acc_cred) : null;
  let acc_save = userData !== null ? format(userData.acc_save) : null;
  return (
    <Card title={t("Balance")}>
      <Form noValidate>
        <Form.Group>
          <Form.Label>{t("Available")}</Form.Label>
          <Form.Text>
            <div style={{ fontSize: "3em" }}>{available}</div>
          </Form.Text>
        </Form.Group>
        <Form.Group>
          <ul className="list-group">
            <li className="list-group-item d-flex justify-content-between align-items-center">
              <div style={{}}>{t("Current account")}</div>
              <span id="acc_curr" className="badge badge-light badge-pill">
                {acc_curr}
              </span>
            </li>
            <li className="list-group-item d-flex justify-content-between align-items-center">
              <div style={{}}>{t("Credit account")}</div>
              <span id="acc_cred" className="badge badge-light badge-pill">
                {acc_cred}
              </span>
            </li>
            <li className="list-group-item d-flex justify-content-between align-items-center">
              <div style={{}}>{t("Savings account")}</div>
              <span id="acc_save" className="badge badge-light badge-pill">
                {acc_save}
              </span>
            </li>
          </ul>
        </Form.Group>
        <Button type="submit">{t("Transfer")}</Button>
      </Form>
    </Card>
  );
};
