import "./Card.css";
import React, { cloneElement } from "react";
import Card from "react-bootstrap/Card";
import Nav from "react-bootstrap/Nav";
// import { Container, Row, Col, Button } from "react-bootstrap";
// import Button from "react-bootstrap/Button";

export default args => {
  let defaults = {
    title: "Untitled",
    navigation: undefined,
    footer: undefined,
    onNavigationClick: () => {}
  };
  let props = Object.assign(defaults, args);

  let key = 0;

  let footer = props.footer ? <Card.Footer>{props.footer}</Card.Footer> : "";

  return (
    <Card>
      {/* <Card.Img variant="top" src="holder.js/100px180?text=Image cap" /> */}
      <Card.Header>
        <Card.Title>{props.title}</Card.Title>
        <Nav variant="tabs" defaultActiveKey={props.navigationdefaultActiveKey}>
          {props.navigation &&
            props.navigation.map((item, key) => (
              <Nav.Item key={key}>
                <Nav.Link
                  href={item.link}
                  onClick={() => props.onNavigationClick(item.link)}
                  style={{ color: "black" }}
                >
                  {item.name}
                </Nav.Link>
              </Nav.Item>
            ))}
        </Nav>
      </Card.Header>
      <Card.Body>
        {React.Children.map(args.children, child => {
          if (typeof child !== "object") {
            return child;
          } else {
            key++;
            return cloneElement(child, {
              styles: null,
              key: key
            });
          }
        })}
      </Card.Body>
      {footer}
    </Card>
  );
};
