import React from "react";
import { useTranslation, Trans } from "react-i18next";
import AccountRegistration from "../accountRegistration";

import Card from "../card";
import { Balance, Transfer } from "../transfer";

export default data => {
  /**
   * Format number
   * @param {number} number
   */
  const format = (number, currency = "Z") => {
    return (
      new Intl.NumberFormat(data.language, {
        notation: "standard",
        minimumFractionDigits: 2
      }).format(number) +
      " " +
      currency
    );
  };

  // const onTransferTypeChange = id => {
  //   console.log("setTransferType: " + id);
  //   // setTransferType(id);
  //   onTransferTypeChange(id);
  // };

  const { t } = useTranslation("LogIn");
  return (
    <>
      <h3>{t("Summary")}</h3>
      <div className="container-fluid">
        <div className="row">
          <div className="col-sm">
            <AccountRegistration data={data} />
          </div>

          <div className="col-sm">
            <Balance data={data} format={format} />

            <Transfer
              data={data}
              format={format}
              type={data.transferType}
              onTypeChange={data.onTransferTypeChange}
            />
          </div>

          <div className="col-sm">
            <Card title={t("Tell us what you think")}>
              <Trans i18nKey="Summary:Please take a moment to let us know what you think">
                Please take a moment to let us know what you think about the
                redesigned display of information on this page. Thanks!
              </Trans>
            </Card>
          </div>
        </div>
      </div>
    </>
  );
};
