import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Form, InputGroup, Button } from "react-bootstrap";
import Card from "../card";
import { Formik } from "formik";
import * as yup from "yup";
import Dropzone from "../dropzone";

export default data => {
  data = data.data;
  const { t } = useTranslation("AccountRegistration");

  const schema = yup.object({
    personalID: yup.string().required(),
    terms: yup.bool().required()
  });

  return (
    <Card title={t("Register account")}>
      <Formik
        validationSchema={schema}
        onSubmit={console.log}
        initialValues={{
          personalID: ""
        }}
      >
        {({
          handleSubmit,
          handleChange,
          handleBlur,
          values,
          touched,
          isValid,
          errors
        }) => (
          <Form noValidate onSubmit={handleSubmit}>
            <Form.Group controlId="validationCustom01">
              <Form.Label>{t("Photo ID")}</Form.Label>
              <Form.Text>
                <Trans i18nKey="SignUp:Photo of me holding my passport">
                  Photo of me holding my passport. The personal identification
                  number must be clearly legible.
                </Trans>
              </Form.Text>
              <br />

              <Dropzone data={data} />
            </Form.Group>

            <Form.Group controlId="exampleForm.SelectCustom">
              <Form.Label>{t("Country")}</Form.Label>
              <Form.Control as="select" custom>
                <option>Latvija</option>
                <option>Deutschland</option>
              </Form.Control>
            </Form.Group>

            <Form.Group controlId="validationFormikUsername">
              <Form.Label>{t("Personal ID")}</Form.Label>
              <InputGroup>
                <InputGroup.Prepend>
                  <InputGroup.Text id="inputGroupPrepend">ID</InputGroup.Text>
                </InputGroup.Prepend>
                <Form.Control
                  type="text"
                  placeholder={t("XXXXXX-XXXXX")}
                  aria-describedby="inputGroupPrepend"
                  name="personalID"
                  value={values.personalID}
                  onChange={handleChange}
                  isInvalid={!!errors.personalID}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.personalID}
                </Form.Control.Feedback>
              </InputGroup>
            </Form.Group>
            <Form.Group>
              <Form.Check
                required
                name="terms"
                label="Agree to terms and conditions"
                onChange={handleChange}
                isInvalid={!!errors.terms}
                feedback={errors.terms}
                id="validationFormik0"
              />
            </Form.Group>
            <Button type="submit">{t("Register")}</Button>
          </Form>
        )}
      </Formik>
    </Card>
  );
};
