import React, { useCallback, useMemo, useState } from "react";
import { useDropzone } from "react-dropzone";

import { Figure } from "react-bootstrap";

const baseStyle = {
  flex: 1,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  padding: "20px",
  borderWidth: 2,
  borderRadius: 2,
  borderColor: "#eeeeee",
  borderStyle: "dashed",
  backgroundColor: "#fafafa",
  color: "#bdbdbd",
  outline: "none",
  transition: "border .24s ease-in-out"
};

const activeStyle = {
  borderColor: "#2196f3"
};

const acceptStyle = {
  borderColor: "#00e676"
};

const rejectStyle = {
  borderColor: "#ff1744"
};

export default data => {
  data = data.data;
  let accountPhotoPath = data.accountPhotoPath;
  if (accountPhotoPath !== null) {
    baseStyle.padding = 0;
    baseStyle.borderStyle = "none"; // "solid";
  }
  const [setFiles] = useState(null);
  // const [thisData] = useState(data);
  // console.log("THIS DATA =====");

  // console.log(data);

  const onDrop = useCallback(acceptedFiles => {
    // Do something with the files
    console.log(acceptedFiles);
    setFiles(acceptedFiles);
    // console.log(data.language);
    data.sendData({
      req: "upload file",
      uuid: "STA",
      room: data.userRoom,
      data: acceptedFiles
    });
    // data.sendData("test");
    // eslint-disable-next-line
  }, []);

  const {
    getRootProps,
    getInputProps,
    isDragActive,
    isDragAccept,
    isDragReject
  } = useDropzone({
    accept: "image/*",
    onDrop
  });

  // const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });
  // const { getRootProps, getInputProps, isDragActive } = useDropzone({
  //   onDrop
  // });

  const style = useMemo(
    () => ({
      ...baseStyle,
      ...(isDragActive ? activeStyle : {}),
      ...(isDragAccept ? acceptStyle : {}),
      ...(isDragReject ? rejectStyle : {})
    }),
    [isDragActive, isDragAccept, isDragReject]
  );

  // useEffect(() => {
  //   if (files === null) return;
  //   // data.sendData("upload file", "STA", data.userRoom, files);
  //   // console.log("THIS DATA =====");
  //   // console.log(data);

  //   // eslint-disable-next-line
  // }, [files]);

  if (accountPhotoPath === null) {
    return (
      <div className="container">
        <div {...getRootProps({ style })}>
          <input {...getInputProps()} />
          <p>Drag 'n' drop some files here, or click to select files</p>
        </div>
      </div>
    );
  } else {
    accountPhotoPath = "http://localhost:9000/" + accountPhotoPath;
    return (
      <div className="container">
        <div {...getRootProps({ style })}>
          <input {...getInputProps()} />
          {/*<img src={accountPhotoPath} width="100%" />*/}
          <Figure>
            <Figure.Image
              width={171}
              height={180}
              alt="171x180"
              src={accountPhotoPath}
            />
            <Figure.Caption>
              Nulla vitae elit libero, a pharetra augue mollis interdum.
            </Figure.Caption>
          </Figure>
        </div>
      </div>
    );
  }
};
