import { useEffect } from "react";
import config from "./config";
import openSocket from "socket.io-client";
let socket = openSocket.connect(config.uri.ENDPOINT, {
  secure: true,
  reconnect: true
});

// Send a chat message
export const sendData = ({ req, uuid, room, data }) => {
  // No data - do not send
  if (req.length === 0) return;

  socket.emit(`onDataReceived`, {
    req: req,
    uuid: uuid,
    room: room,
    data: data
  });
};

// Join a chat room
export const joinRoom = (data, uuid, room) => {
  console.log("joinRoom");

  socket.emit(`join`, {
    data: data,
    uuid: uuid,
    room: room,
    req: "join room"
  });
};

// Leave the chat room
export const leaveRoom = (data, uuid, room) => {
  socket.emit(`leave`, {
    char: data,
    uuid: uuid,
    room: room
  });
};

