export const useSocket = (uuid, onDataReceived) => {
  // Listen for a data from server
  const SuranadiraSocket = (onDataReceived, uuid) => {
    socket.on(`onDataReceived`, data => {
      onDataReceived(data);
    });
  };

  useEffect(() => {
    SuranadiraSocket(onDataReceived, uuid);
    // eslint-disable-next-line
  }, [uuid]);
};
