import React from "react";
import { useTranslation } from "react-i18next";
import { Form, InputGroup, Button } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import Card from "../card";

export default args => {
  // let data = args.data;
  let format = args.format;
  let navigation = args.navigation;
  let navigationdefaultActiveKey = args.navigationdefaultActiveKey;
  let userData = args.data.userData;
  let onNavigationClick = args.onNavigationClick;
  const { t } = useTranslation("Transfer");

  const schema = yup.object({
    fromAccount: yup.string().required(t("From Account is required")),
    toPersonalID: yup
      .string()
      .required(t("Recipients Personal ID is required")),
    amount: yup.string().required(t("Amount is required"))
  });

  let acc_curr = "";
  let acc_cred = "";
  let acc_save = "";
  if (userData !== null) {
    acc_curr = " (" + format(userData.acc_curr) + ")";
    acc_cred = " (" + format(userData.acc_cred) + ")";
    acc_save = " (" + format(userData.acc_save) + ")";
  }

  return (
    <Card
      title={t("Transfer to account")}
      navigation={navigation}
      navigationdefaultActiveKey={navigationdefaultActiveKey}
      onNavigationClick={onNavigationClick}
    >
      <Formik
        validationSchema={schema}
        onSubmit={console.log}
        initialValues={{
          fromAccount: "",
          toPersonalID: "",
          amount: ""
        }}
      >
        {({
          handleSubmit,
          handleChange,
          handleBlur,
          values,
          touched,
          isValid,
          errors
        }) => (
          <Form noValidate onSubmit={handleSubmit}>
            <Form.Group controlId="validationFromAccount">
              <Form.Label>{t("From account")}</Form.Label>
              <Form.Control
                as="select"
                custom
                name="fromAccount"
                onChange={handleChange}
                isInvalid={!!errors.fromAccount && touched.fromAccount}
              >
                <option value="">{t("Choose...")}</option>
                <option value="1">
                  {t("Current account")}
                  {acc_curr}
                </option>
                <option value="2">
                  {t("Credit account")}
                  {acc_cred}
                </option>
                <option value="3">
                  {t("Savings account")}
                  {acc_save}
                </option>
              </Form.Control>
              <Form.Control.Feedback type="invalid">
                {errors.fromAccount}
              </Form.Control.Feedback>
            </Form.Group>
            <Form.Group controlId="validationFormikToPersonalID">
              <Form.Label>{t("To Personal ID")}</Form.Label>
              <InputGroup>
                <InputGroup.Prepend>
                  <InputGroup.Text id="inputGroupPrepend">ID</InputGroup.Text>
                </InputGroup.Prepend>
                <Form.Control
                  type="text"
                  placeholder="XXXXXX-XXXXX"
                  aria-describedby="inputGroupPrepend"
                  name="toPersonalID"
                  value={values.toPersonalID}
                  onChange={handleChange}
                  isInvalid={!!errors.toPersonalID && touched.toPersonalID}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.toPersonalID}
                </Form.Control.Feedback>
              </InputGroup>
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlTextarea1">
              <Form.Label>{t("Description")}</Form.Label>
              <Form.Control as="textarea" rows="3" />
            </Form.Group>
            <Form.Group controlId="validationFormikAmount">
              <Form.Label>{t("Amount")}</Form.Label>
              <InputGroup>
                <InputGroup.Prepend>
                  <InputGroup.Text id="inputGroupPrepend">Z</InputGroup.Text>
                </InputGroup.Prepend>
                <Form.Control
                  // type="number"
                  placeholder={format(0, "")}
                  aria-describedby="inputGroupPrepend"
                  name="amount"
                  value={values.amount}
                  onChange={handleChange}
                  // onBlur={handleChange}
                  isInvalid={!!errors.amount && touched.amount}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.amount}
                </Form.Control.Feedback>
              </InputGroup>
            </Form.Group>
            <Button type="submit">{t("Next")}</Button>
          </Form>
        )}
      </Formik>
    </Card>
  );
};
