import "bootstrap/dist/css/bootstrap.min.css";
import "./Authorized.css";
import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import i18n from "../../translations/i18n";

import LanguageSelector from "../../components/language-selector";
import Summary from "../../components/summary";
// import Balance from "../../components/balance";

import { useSocket, sendData, joinRoom } from "../../utils/sockets";

export default () => {
  const userDataFetchInterval = 30000; // Test: 5000 | 30000;
  const { t } = useTranslation("Authorized");
  const [uuid] = useState("STA");
  const [userRoom] = useState("room1");
  const [connected, setConnected] = useState(false);
  const [tmrConnected, setTmrConnected] = useState(null);
  const [language, setLanguage] = useState(i18n.options.fallbackLng[0]);
  const [transferType, setTransferType] = useState("#u2s");
  // const [userData, setUserData] = useState({
  //   acc_curr: null,
  //   acc_cred: null,
  //   acc_save: null
  // });
  const [userData, setUserData] = useState(null);
  const [accountPhotoPath, setAccountPhotoPath] = useState(null);
  /**
   * Call external hook
   */
  useSocket(uuid, data => {
    console.log(`Received: `);
    console.log(data);
    if (data.req === "join room" && data.status === "success") {
      setConnected(true);
      sendData({ req: "get user", uuid: "STA", room: userRoom });
    } else if (data.req === "get user" && data.status === "ready") {
      // setUserData(JSON.parse(data.data));
      setUserData(data.data);
    } else if (data.req === "upload file" && data.status === "ready") {
      setAccountPhotoPath(data.data + "?r=" + Math.random());
    }
  });

  // const send = data => {
  //   // sendData();
  //   console.log(data);
  // };

  // Hook for new user room
  useEffect(() => {
    joinRoom("hello", uuid, userRoom);
    // eslint-disable-next-line
  }, [userRoom]);

  useEffect(() => {
    if (connected) {
      let tmr = setInterval(() => {
        sendData({ req: "get user", uuid: "STA", room: userRoom });
      }, userDataFetchInterval);
      setTmrConnected(tmr);
    } else {
      clearInterval(tmrConnected);
    }
    // eslint-disable-next-line
  }, [connected]);

  // const getLanguage = () => {
  //   return (
  //     i18n.language ||
  //     (typeof window !== "undefined" && window.localStorage.i18nextLng) ||
  //     "en"
  //   );
  // };

  i18n.on("languageChanged", function(lng) {
    setLanguage(lng);
  });

  const onTransferTypeChange = id => {
    console.log("setTransferType: " + id);
    setTransferType(id);
  };

  const authorizedData = {
    language: language,
    userData: JSON.parse(userData),
    accountPhotoPath: accountPhotoPath,
    userRoom: userRoom,
    sendData: sendData,
    transferType: transferType,
    onTransferTypeChange: onTransferTypeChange
  };

  return (
    <Router>
      <div className="App">
        {/* Header */}
        <nav
          className="navbar navbar-expand-lg navbar-light  "
          style={{ backgroundColor: "#fefefe" }}
        >
          <div className="container">
            <Link className="navbar-brand" to={"/sign-in"}>
              {t("Logo")}
            </Link>
            <div className="collapse navbar-collapse" id="navbarTogglerDemo02">
              <ul className="navbar-nav ml-auto">
                <li className="nav-item">
                  <Link className="nav-link" to={"/summary"}>
                    {t("Summary")}
                  </Link>
                </li>

                <li className="nav-item">
                  <Link className="nav-link" to={"/logout"}>
                    {t("Logout")}
                  </Link>
                </li>

                <li className="nav-item">
                  <div className="nav-link">
                    <LanguageSelector />
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </nav>

        {/* Separator between header and body */}
        <div style={{ height: "20px" }}></div>

        {/* Footer */}

        <div className="auth-wrapper">
          <div className="auth-inner">
            <Switch>
              <Route exact path="/" component={() => Summary(authorizedData)} />
              <Route
                path="/summary"
                component={() => Summary(authorizedData)}
              />
            </Switch>
          </div>
        </div>
      </div>
    </Router>
  );
};
